function Mem(id, scr, texts, color)
{
    var btn ="";
    for (var i in texts) {
    var ii = i - 0 + 1;
    btn+=  `<input id="in` + (ii) + "" + (id) + `" type = "text" class = "ctr_inp">`;
  }


  document.getElementById("root").innerHTML = "<div class = 'mem' >"
    + `<input id="back` + id + `" type = "submit" value="Назад" class = "ctr_inp">`
    + btn + 
    `<canvas id="cvs`+id+`" class = "cvs"></canvas>
    <input id="dvl`+id+`" type = "submit" value="Отправить" class = "ctr_inp"></div> "`; 

    var canvas = document.getElementById("cvs" + id);
    var ctx = canvas.getContext('2d');

    var img = new Image();
    img.src = scr;
    img.onload = () => {
      canvas.height = img.height;
      canvas.width = img.width;

      ctx.drawImage(img,0,0);
      for (var i in texts) {
        ctx.font = "bold  50px Verdana";
        ctx.fillStyle = color;
        ctx.textAlign = "center";
        ctx.fillText(texts[i].text, texts[i].x, texts[i].y);
      }   
    }

    this.write = function (new_texts) {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(img,0,0);
      for (var i in texts) {

        var lable = new_texts[i].value;
        var lll =  lable.length;

        if (lll <= texts[i].max_size) {
          ctx.font = "bold  50px Verdana";
          ctx.fillStyle = color;
          ctx.textAlign = "center";
          ctx.fillText(new_texts[i].value, texts[i].x, texts[i].y);
        }
        else {
          var nnn = lll/texts[i].max_size;
          var words = lable.split(" ");
          var n_words = words.length;
   

          var count = 0

          for (var j = 0; j < nnn; j++) {
          var new_string = "";



           for (var k = 0; k < n_words/nnn; k++) {

            if (words[count] != undefined)
            new_string+= words[count] + " ";
            //console.log (Math.floor(j*n_words/nnn) + k);
            count++;

           } 

           //var newstr = new_string.split("undefined"); 
           //new_string = "";

           //for (var b = 0; b < newstr.length; b++) {
           // new_string+=newstr[i] + " "; 
           //}


            ctx.font = "bold  50px Verdana";
            ctx.fillStyle = color;
            ctx.textAlign = "center";
            ctx.fillText(new_string, texts[i].x, texts[i].y + 40*j);           
          }
        }


      }
    }


    var inp = [];
    for (var i in texts) {
      var ii = i - 0 + 1;
      inp[i] = document.getElementById("in" + ii + "" + id);
      inp[i].value = texts[i].text;
      inp[i].oninput = () =>{
        this.write(inp);
      }
    }

  var dvl = document.getElementById("dvl" + id);

  dvl.onclick = async ()=> {

      await publishOnWall(7245851, canvas.toDataURL());
    //CANVAS_SEND = canvas;
    

    //connect.send("VKWebAppShowWallPostBox", {"message": "Hello!"});
    //download(canvas, 'myimage.png');
    
    //ALERT();

    //connect.send("VKWebAppCallAPIMethod", {"method": "users.get", "request_id": "32test", "params": {"user_ids": "1", "v":"5.103", "access_token":"your_token"}});
   /* connect.send("VKWebAppShowImages", {
      images: [
        canvas.toDataURL("image/png;base64"),
      ]
    });
    */






    //var s =  connect.send("VKWebAppCallAPIMethod", {"method": "photos.getWallUploadServer", "request_id": "mem", "params": { "v":"5.103", "access_token":ACCESS_TOKEN}});
    //alert(s);
  }

  var back = document.getElementById("back" + id);
  back.onclick = () =>{
    start();
  }
}

function load_mem(id, scr, texts, color){
  var img = new Image();
  img.src = scr;
  img.className = "mem_img";
  img.id = "mem_img" + id;
  document.getElementById("root").append(img);
  var img =  document.getElementById("mem_img" + id);
  img.onclick = () => {
    new Mem(id, scr, texts, color);
  }
}

function start() {
  document.getElementById("root").innerHTML = "";
  new load_mem(1, "mem1.jpg", [{text: "No", x: 700, y:100, max_size: 7}, {text: "Yes", x: 700, y:600, max_size: 7}], "#000000");
  new load_mem(2, "mem5.jpg", [{text: "Oyhh", x: 350, y:350, max_size: 20}], "#FFFFFF");
  new load_mem(3, "mem6.jpg", [{text: "Lapotaa", x: 350, y:350,max_size: 20}], "#FFFFFF");



}




function main()
{

  start();
  connect.send("VKWebAppGetAuthToken", {"app_id": 7245851, "scope":  "friends,photos"});
}

main();





// скаченая непонятная залупа 
function download(canvas, filename) {
  var lnk = document.createElement('a'), e;
  lnk.download = filename;
  lnk.href = canvas.toDataURL("image/png;base64");
  if (document.createEvent) {
    e = document.createEvent("MouseEvents");
    e.initMouseEvent("click", true, true, window,
                     0, 0, 0, 0, 0, false, false, false,
                     false, 0, null);

    lnk.dispatchEvent(e);
  } else if (lnk.fireEvent) {
    lnk.fireEvent("onclick");
  }
}






 async function publishOnWall(APP_ID, wallPhoto) {
        const authToken = await connect.send("VKWebAppGetAuthToken", {
            "app_id": APP_ID,
            "scope": "photos"
        });
        const serverToPhoto = await connect.send("VKWebAppCallAPIMethod", {
            method: "photos.getWallUploadServer",
            params: {access_token: authToken.data.access_token, v: "5.95"}
        });
        const uploadUrl = serverToPhoto.data.response.upload_url;
        const request = new FormData();
        request.append("photo", await fetch(wallPhoto, "story.png", 'image/png'));
        const {server, photo, hash} = await fetch(CORS_PROXY + uploadUrl, {
            method: "POST",
            body: request
        }).then((response) => response.json());
        const res = await connect.send("VKWebAppCallAPIMethod", {
            method: "photos.saveWallPhoto",
            params: {
                access_token: authToken.data.access_token, server, photo, hash,
                v: "5.95"
            }
        });
        const {id: photo_id, owner_id} = res.data.response[0];
        await connect.send("VKWebAppShowWallPostBox", {
            message: `Сообщение на стену с фото`,
            attachments: `photo${owner_id}_${photo_id},https://vk.com/app7014374`
        });
    }